import titles from '../../components/title.vue'
import userSevers from '@/api/userinfo.js'
import {
	mapActions
} from 'vuex'
export default {
	data() {
		return {
			titles: '登录文章助手',
			url: "",
			token: '',
			urlHeader: "",
		}
	},
	created(option) {

		this.url = window.location.href
		const num1 = this.url.indexOf("=")
		const num2 = this.url.indexOf("#")
		const num3 = this.url.indexOf('?')
		const remove = this.url.substring(num3, num2)
		this.token = this.url.substring(num1 + 1, num2);
		this.urlHeader = this.url

		this.urlHeader = this.urlHeader.replace(remove, '')
		console.log(this.urlHeader)
		this.getURL()
		// this.goRoute()
		this.goRoute()
	},
	methods: {
		...mapActions(['getToken']),
		getURL() {
			try {
				const value = uni.getStorageSync('nextURL');
				if (value) {
					this.url = value.path
					console.log(this.url)
				}
			} catch (e) {
				// error
			}
		},
		// update() {
		// 	window.location.href = this.urlHeader
		// 	this.goRoute()
		// },
		goRoute() {
			this.getToken(this.token)
			
			setTimeout(() => {
				uni.reLaunch({
					url: this.url
				});
			}, 2000)
		}

	},
	components: {
		titles
	}
}
